@import url(https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&display=swap);
.header-container {
  background-color: #10162f;
  padding: 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.header-container__logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.header-container__logo h1 {
  margin-top: 40px;
}

.header-container__logo img {
  width: 120px;
  height: 100px;
}

.header-container__score-info {
  color: white;
  margin-right: 2rem;
  padding: 0;
}

.header-container__score-info li {
  list-style: none;
}

.la-star {
  color: yellow;
}

@media (max-width: 768px) {
  .header-container {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .header-container__logo {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .header-container__logo h1 {
    margin-top: 0;
    margin-bottom: 0;
  }

  .header-container__score-info {
    display: -webkit-flex;
    display: flex;
    margin-right: 0;
  }

  .header-container__score-info li {
    -webkit-align-self: center;
            align-self: center;
    padding-right: 1rem;
  }
}

.card {
  padding: 1rem;
  text-align: center;
  background-color: #ffd50074;
  cursor: pointer;
  margin: 1em;
  transition: all 0.3s ease-in-out;
}

.card:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.card hr {
    border: none;
    height: 1px;
    background-color: #10162f;
}

.card-list-container {
  display: -webkit-flex;
  display: flex;
  width: 50%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 2rem auto;
  max-width: 1220px;
}

.card-list-container img {
  width: 120px;
  height: 120px;
}


@media(max-width: 768px) {
  .card-list-container {
    width: 100%;
  }
}


* {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Courier Prime', monospace, Arial 
}

